@import url("https://fonts.googleapis.com/css?family=Caveat");
* {
  margin: 0;
  padding: 0;
  outline: none;
}
body,
html {
  width: 100%;
  height: 100%;
}
header {
  width: 100%;
  padding: 10px 0.5% 0px 0.5%;
  float: left;
  background: transparent;
  position: fixed;
  background-color: black;
  border-radius: 10px;
  margin-bottom: 100px;
}
#logo {
  width: 100%;
  font-size: 2em;
  text-align: center;
  color: white;
  cursor: pointer;
  font-family: "Caveat", serif;
  width: 100%;
}

#scroll {
  background-color: white;
  color: black;
}

#top {
  width: 100%;
  height: 700px;
  background-image: url(http://energosberejenie.org/images/samye-energosberegayushchie-kholodilniki.jpg);
  background-blend-mode: multiply;
  background-size: cover;
  background-color: #2e3a44;
  text-align: center;
  color: white;
  font-size: 2em;
}
#rec {
  text-align: center;
  height: 400px;
  width: 600px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10%;
  background-color: white;
  border-radius: 10px;
}
#text {
  color: black;
}

.fixed {
  z-index: 5000;
  background-color: #fff;
  border-bottom: 1px solid silver;
}
header.fixed #logo {
  color: #343434;
}

.footer {
  height: 150px;
  width: 100%;
}

#react {
  border: 1px silver solid;
}
